import {
    loadAllComfyOptions,
    validateComfyConnection,
    clearOptionsCache,
} from '../services/api-service';
import {
    getSettings,
    populateSelectOptions,
    clearAllOptions,
    FIXED_OPTIONS,
    saveSetting,
} from '../services/ui-manager';
import { updateWorkflowSelect } from '../services/workflow-manager';
import { getExtensionRoot } from '../../utils/dom-utils';

const DEFAULT_COMFY_URL = 'http://127.0.0.1:8188';

/**
 * ComfyUI
 */
export async function populateComfyOptions(): Promise<void> {
    const settings = getSettings();

    if (!settings.comfyUrl) {
        clearAllOptions();
        return;
    }

    try {
        const { models, samplers, schedulers, vaes } = await loadAllComfyOptions(settings);

        populateSelectOptions('sd_model', models, '', settings.sd_model);
        populateSelectOptions('sd_sampler', samplers, '', settings.sd_sampler);
        populateSelectOptions('sd_scheduler', schedulers, '', settings.sd_scheduler);
        populateSelectOptions('sd_vae', vaes, 'VAE', settings.sd_vae);

        const $root = getExtensionRoot();
        const resolutionSelect = $root.find('#sd_resolution');
        resolutionSelect.empty();
        FIXED_OPTIONS.resolutions.forEach(option => {
            const selected =
                option.value === (settings.sd_resolution || 'sd_res_1024x1024') ? ' selected' : '';
            resolutionSelect.append(
                `<option value="${option.value}"${selected}>${option.text}</option>`
            );
        });
    } catch (error) {
        log.error('Failed to load ComfyUI options:', error);
        clearAllOptions();
    }
}

/**
 *  ComfyUI 
 */
export async function validateComfyUrl(): Promise<void> {
    const $root = getExtensionRoot();
    let url = ($root.find('#comfy-url-input').val() as string) || DEFAULT_COMFY_URL;

    clearOptionsCache();
    url = normalizeComfyBaseUrl(url);
    const button = $root.find('#comfy-validate-btn');
    const status = $root.find('#comfy-connection-status');
    const originalText = button.text();

    button.text('...').prop('disabled', true);
    if (status && status.length) status.hide();

    if (!url) {
        toastr.error(' ComfyUI URL');
        button.text(originalText).prop('disabled', false);
        return;
    }

    saveSetting('comfyUrl', url);

    try {
        const isValid = await validateComfyConnection(url);
        if (!isValid) {
            toastr.error('ComfyUI ');
            return;
        }

        toastr.success('ComfyUI API connected.');

        await populateComfyOptions();
        await updateWorkflowSelect();
    } catch (err: any) {
        toastr.error(`ComfyUI ${err?.message || ''}`);
    } finally {
        button.text(originalText).prop('disabled', false);
    }
}

/**
 *  ComfyUI 
 */
export function normalizeComfyBaseUrl(input: string): string {
    let url = (input || '').trim();
    if (!/^https?:\/\//i.test(url)) {
        url = `http://${url}`;
    }
    url = url.replace(/\/$/, '');
    return url;
}
